import React from "react";
import  {getMergeSortAnimation} from '../sortingAlgorithms/sortingAlgorithms.js'
import  {getBubbleSortAnimation} from '../sortingAlgorithms/sortingAlgorithms.js'
import  {getQuickSortAnimation} from '../sortingAlgorithms/sortingAlgorithms.js'
import './SortingVisualizer.css';
import Box from '@mui/material/Box';
import Slider from '@mui/material/Slider';

const ANIMATION_SPEED_MS = 70;
let ARRAY_SIZE = window.innerWidth <1000 ? 20:40;
const ARRAY_HEIGHT = window.innerHeight/2;
let ARRAY_WIDTH = Math.min(800,window.innerWidth/3)/(ARRAY_SIZE+1);

const PRIMARY_COLOR = 'turquoise';
const SECONDARY_COLOR = 'red';
//----array sizee range
let MIN_SIZE = 10
let MAX_SIZE = window.innerWidth <1000 ? 60:80;

export default class SortingVisualizer extends React.Component{
    constructor(props){
        super(props);
        this.state={
            array: [],
            isButtonDisabled: false,
        };
    }
    componentDidMount(){
        window.addEventListener("resize", this.resize.bind(this));
        this.resize();
        this.resetArray();
    }
    resize() {
        if(window.innerWidth <1000)
        {            
            MAX_SIZE = 40;
            ARRAY_SIZE =20;
        }
        else
        {
            MAX_SIZE = 60;
            ARRAY_SIZE = 40;
        }
        ARRAY_WIDTH = Math.min(800,window.innerWidth/3)/(ARRAY_SIZE+1);
    }
    resetArray(){
        const array=[];
        for(let i=0;i<ARRAY_SIZE;i++)
        {
            array.push(Math.floor(Math.random()*(ARRAY_HEIGHT-5+1)+5));
        }
        this.setState({array: array});
    }
    mergeSort() {
        this.disable(true);
        const animations=getMergeSortAnimation(this.state.array);
        for(let i=0;i<animations.length;i++)
        {
            const arrayBars = document.getElementsByClassName('array-bar');
            const isColorChange = i%3 !== 2;
            if(isColorChange)
            {
                const [barOneInd,barTwoInd] = animations[i];
                const barOneStyle = arrayBars[barOneInd].style;
                const barTwoStyle = arrayBars[barTwoInd].style;
                const color = i%3===0?'red': 'turquoise';
                setTimeout(() => {
                    barOneStyle.backgroundColor = color;
                    barTwoStyle.backgroundColor = color;
                },i* ANIMATION_SPEED_MS);
            }
            else
            {
                setTimeout(() => {
                const [barOneInd,newHeight] = animations[i];
                const barOneStyle = arrayBars[barOneInd].style;
                barOneStyle.height =`${newHeight}px`;
                },i*ANIMATION_SPEED_MS);
            }
        }
        
        setTimeout(() => {
            this.disable(false);
            },animations.length * ANIMATION_SPEED_MS);
    }
    qucikSort() {
        
        this.disable(true);
        const animations=getQuickSortAnimation(this.state.array);
        for(let i=0;i<animations.length;i++)
        {
            const arrayBars = document.getElementsByClassName('array-bar');
            const [cond,barOneInd,clr] = animations[i];
            const isColorChange = cond===0;
            if(isColorChange)
            {
                const barOneStyle = arrayBars[barOneInd].style;
                const color = clr === 1 ?'red': 'turquoise';
                setTimeout(() => {
                    barOneStyle.backgroundColor = color;
                },i* ANIMATION_SPEED_MS);
            }
            else
            {
                setTimeout(() => {
                const barOneStyle = arrayBars[barOneInd].style;
                barOneStyle.height =`${clr}px`;
                },i*ANIMATION_SPEED_MS);
            }
        }
        
        setTimeout(() => {
            this.disable(false);
            },animations.length * ANIMATION_SPEED_MS);
    }
    heapSort() {}
    bubbleSort() {
        
        this.disable(true);
        const animations=getBubbleSortAnimation(this.state.array);
        for(let i=0;i<animations.length;i++)
        {
            const arrayBars = document.getElementsByClassName('array-bar');
            const [cond,barOneInd,barTwoInd] = animations[i];
            const isColorChange = cond===0;
            if(isColorChange)
            {
                const barOneStyle = arrayBars[barOneInd].style;
                const barTwoStyle = arrayBars[barTwoInd].style;
                const color = i%2 === 0 ?'red': 'turquoise';
                setTimeout(() => {
                    barOneStyle.backgroundColor = color;
                    barTwoStyle.backgroundColor = color;
                },i* ANIMATION_SPEED_MS);
            }
            else
            {
                setTimeout(() => {
                const barOneStyle = arrayBars[barOneInd].style;
                barOneStyle.height =`${barTwoInd}px`;
                },i*ANIMATION_SPEED_MS);
            }
        }
        
        setTimeout(() => {
            this.disable(false);
            },animations.length * ANIMATION_SPEED_MS);
    }
    
disable(cond){
    document.getElementById("gen-button").disabled = cond;
    document.getElementById("merge-button").disabled = cond;
    document.getElementById("quck-button").disabled = cond;
    document.getElementById("heap-button").disabled = cond;
    document.getElementById("bubble-button").disabled = cond;
}
    render(){
        const {array} = this.state;
        const handleChange = (event, newValue) => {
            ARRAY_SIZE=newValue;
            ARRAY_WIDTH = Math.min(800,window.innerWidth/3)/(ARRAY_SIZE+1);
        };
        return (
        <div className="main-page" style={{ width: `${ARRAY_WIDTH*ARRAY_SIZE*2}px`}}>
            <div className="array-container" style={{ width: `${ARRAY_WIDTH*ARRAY_SIZE*2}px`, height: `${ARRAY_HEIGHT}px`}}>
            {array.map((value,ind)=>(
                    <div className="array-bar" key={ind} style={{
                        backgroundColor: 'turquoise', width: `${ARRAY_WIDTH}px` ,height: `${value}px`}}></div>
            ))}
            <div className="imaginary-bar" style={{
                width: `${ARRAY_WIDTH}px` ,height: `${ARRAY_HEIGHT}px`}}></div>
            </div>
            <div className="Buttons">
            <button onClick={()=> this.resetArray() }  className= "button-18" id="gen-button">Generate Array</button>
            <button onClick={()=> this.mergeSort() }  className= "button-18" id = "merge-button">Merge Sort</button>
            <button onClick={()=> this.qucikSort()}  className= "button-18" id = "quck-button">Quick Sort</button>
            <button onClick={()=> this.heapSort()}  className= "button-18" id = "heap-button">Heap Sort</button>
            <button onClick={()=> this.bubbleSort()}  className= "button-18" id="bubble-button">Bubble Sort</button>
            </div>
            <div>
            <Box width={100}  className = "sliders">
                <Slider defaultValue={ARRAY_SIZE} min = {MIN_SIZE} max={MAX_SIZE} aria-label="Default" valueLabelDisplay="auto" onChange ={handleChange}/>
            </Box>
            </div>
            </div>
        );
    }
}