
export function getMergeSortAnimation(array){
    const animations =[];
    if(array.length<=1)return array;
    const auxiliaryArray =array.slice();
    mergeSortHelper(array,0,array.length-1,auxiliaryArray,animations);
    return animations;
}

function mergeSortHelper(
    mainArray,
    startInd,
    endInd,
    auxiliaryArray,
    animations,){
        if(startInd===endInd) return;
        const middleInd = Math.floor((startInd+endInd)/2);
        mergeSortHelper(auxiliaryArray,startInd,middleInd,mainArray,animations);
        mergeSortHelper(auxiliaryArray,middleInd+1,endInd,mainArray,animations);
        doMerge(mainArray,startInd,middleInd,endInd,auxiliaryArray,animations);
}

function doMerge(
    mainArray,
    startInd,
    middleInd,
    endInd,
    auxiliaryArray,
    animations,
){
    let k = startInd;
    let i = startInd;
    let j = middleInd+1;
    while(i<=middleInd && j <= endInd){
        animations.push([i,j]);
        animations.push([i,j]);
        if(auxiliaryArray[i] <=auxiliaryArray[j]){
            animations.push([k,auxiliaryArray[i]]);
            mainArray[k++]=auxiliaryArray[i++];
        }
        else{
            animations.push([k,auxiliaryArray[j]]);
            mainArray[k++]=auxiliaryArray[j++];
        }
    }
    while(i<=middleInd){
        animations.push([i,i]);
        animations.push([i,i]);
        animations.push([k,auxiliaryArray[i]]);

        mainArray[k++]=auxiliaryArray[i++];
    }
    while(j<=endInd){
        animations.push([j,j]);
        animations.push([j,j]);
        animations.push([k,auxiliaryArray[j]]);

        mainArray[k++]=auxiliaryArray[j++];
    }
}

//--------bubble sort
export function getBubbleSortAnimation(array){
    const animations =[];
    bubbleSortHelper(array,0,array.length-1,animations);
    return animations;
}

function bubbleSortHelper(
    arr,
    startInd,
    endInd,
    animations,){
        for(let i=startInd;i<endInd;i++)
        {
            for(let j=startInd;j<endInd-i;j++)
            {
            animations.push([0,j,j+1]);
            animations.push([0,j,j+1]);
                if(arr[j]>arr[j+1])
                {
                    animations.push([1,j,arr[j+1]]);
                    animations.push([1,j+1,arr[j]]);
                    let temp=arr[j];
                    arr[j]=arr[j+1];
                    arr[j+1]=temp;
                }
            }
        }
}
//-----quick sort
export function getQuickSortAnimation(array){
    const animations =[];
    quickSortHelper(array,0,array.length-1,animations);
    return animations;

}
function quickSortHelper(
    arr,
    startInd,
    endInd,
    animations,){
        if(startInd < endInd)
        {
            let piv = partition(arr,startInd,endInd,animations);
            quickSortHelper(arr,startInd,piv-1,animations);
            quickSortHelper(arr,piv+1,endInd,animations);
        }
}
function partition(
    arr,
    startInd,
    endInd,
    animations,
){
    let pivot = arr[endInd];
    animations.push([0,endInd,1]);
    let i = (startInd-1);
    for(let j = startInd;j<=endInd-1;j++)
    {
        animations.push([0,j,1]);
        animations.push([0,j,0]);
        if(arr[j]<pivot)
        {
            i++;
            animations.push([1,i,arr[j]]);
            animations.push([1,j,arr[i]]);
            let temp=arr[i];
            arr[i]=arr[j];
            arr[j]=temp;
        }
    }
    animations.push([1,i+1,arr[endInd]]);
    animations.push([1,endInd,arr[i+1]]);
    let temp=arr[i+1];
    arr[i+1]=arr[endInd];
    arr[endInd]=temp;
    animations.push([0,endInd,0]);
    return i+1;
}